import { SEARCH_USER_HISTORY } from '@/common/storageKeys';
import { request } from "@/utils/request";
import { REQUEST_SUCCEEDED_CODE } from "@/common/constants";
/**
 * searchUser 搜索用户界面-通过昵称查询用户
 * @description 搜索用户界面-通过昵称查询用户
 * @Author: 穆兰
 * @Date: 2022/1/17
 * @LastEditors: 穆兰
 * @LastEditTime: 2022/1/17
 */
export default {
    data() {
        return {
            searchText: '',
            searchHistory: [],
            searchResult: [],
            showSeacrhHistory: true
        };
    },
    async onLoad() {
        let searchHistory = await uni.getStorageSync(SEARCH_USER_HISTORY);
        console.log("searchHistory", searchHistory);
        if (searchHistory) {
            this.searchHistory = searchHistory;
        }
    },
    methods: {
        /**
         *@description 点击返回按钮
         */
        onNarLeftClick() {
            uni.navigateBack({
                delta: 1
            });
        },
        /**
         * @description 点击历史搜索文字
         * @param {string} labelText 文字
         */
        onHistoryTextClick(labelText) {
            this.searchText = labelText;
            this.searchUsers(this.searchText);
        },
        /**
         * @description 搜索框输入时
         */
        onSearchInput(e) {
            this.searchText = e;
        },
        /**
         * @description 点击搜索按钮时
         */
        onSearchConfirm() {
            if (this.searchText.length < 1) {
                uni.showToast({
                    title: "请先输入关键词"
                });
                return;
            }
            this.searchUsers(this.searchText);
        },
        /**
         * @description 根据关键词搜索动态
         * @param {string} username 搜索的关键词(用户名)
         */
        async searchUsers(username) {
            uni.showLoading({
                title: "搜索中"
            });
            let res = await request({
                data: {
                    method: 'GET',
                    group: 'social',
                    action: 'searchUser',
                    data: {
                        username: username
                    },
                    header: {
                        'content-type': 'application/x-www-form-urlencoded',
                    },
                }
            });
            uni.hideLoading();
            if (res.data.code === REQUEST_SUCCEEDED_CODE) {
                let users = res.data.data;
                console.log(users);
                this.searchResult = users;
                this.showSeacrhHistory = false;
            }
            else {
                console.log('searchActivities', res.data);
            }
            let searchHistory = uni.getStorageSync(SEARCH_USER_HISTORY);
            if (!searchHistory) {
                searchHistory = [];
            }
            if (searchHistory.indexOf(username) == -1) {
                searchHistory.push(username);
                if (searchHistory.length > 10) {
                    searchHistory.splice(0, 1);
                }
            }
            else {
                searchHistory.splice(searchHistory.indexOf(username), 1);
                searchHistory.push(username);
            }
            uni.setStorage({
                key: SEARCH_USER_HISTORY,
                data: searchHistory
            });
            this.searchHistory = searchHistory.reverse();
        }
    }
};
